
.wrap {
    width: 418px;
    padding: 30px;
    border: 1px solid black;
    text-align: center;
    margin: 0 auto;
    border-radius: 10px;
}
  
.title {
    text-align: center;
    /* outline: 1px solid red; */
    padding: 5px;
    margin: 0;
    color: #374D5C;
}

.count_button {
    border: none;
    padding: 10px 15px;
    background-color: #92B5DF;
    color: #fff;
    font-weight: 500;
    margin-left: 5px;
    margin-right: 5px;
    font-family: inherit;
    cursor: pointer;
    outline: none;
    box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
    transition: box-shadow 250ms linear;
    border-radius: 10px;
}
  
.count_button:active {
    box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);
}
  
.change_number {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 64px;
    font-family: monospace;
    text-align: center;
    color: #374D5C;
}